@font-face {
  font-family: 'New York';
  src: url(./NewYork.otf);
}
@font-face {
  font-family: 'Bubasaur';
  src: url(./Bubasaur.ttf);
}
@import url('https://fonts.googleapis.com/css2?family=Erica+One&display=swap');

body
{
  background-color: #c7f0ff;
  background-position: center;
  background-size: cover;
}
.App {
  padding: 1em;
  text-align: center;
  width: 80%;
  margin: 0 auto;
  user-select: none;
}

h1,h2,h3,h4,h5,a,p
{
  font-family: 'bubasaur', cursive;
  
}
a
{
  transition: 0.6s cubic-bezier(.11,.45,.22,1.28) !important; 
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
nav
{
  background-color: rgb(220, 246, 255);
  height: 150px;
  width: 100%;
  margin-bottom: 3em;
  display: grid;  padding: 2em !important;
  place-items: center;
  font-size: 2em;
  border-radius: 1em;
  z-index: 1 !important;
  filter: drop-shadow(32px 32px 24px rgba(39, 134, 145, 0.25)) !important;
  box-shadow: inset -6px -12px 16px 0 rgba(65, 178, 190, 0.25);

}
a:hover
{
  color: #ffffff;
  text-decoration: none;
  filter: drop-shadow(20px 20px 16px #61dafb60);
}
a,table
{
  color: rgb(112,208,236) !important;
}
.statistics
{
  margin-top: 2em;
  border-radius: 2em;
  align-items: center !important;
  z-index: 9999 !important;
  

}
.statistics-text
{
  background-color: rgb(214, 245, 255);
  display: grid;
  border-radius: 2em !important;
  grid-template-columns: 1fr 1fr;
  place-items: center;
  text-align: left;
  height: 500px;
  filter: drop-shadow(32px 32px 24px rgba(39, 134, 145, 0.25));
  box-shadow: inset -6px -12px 16px 0 rgba(65, 178, 190, 0.25);
}
.jumbotron
{
  padding: 32px 32px !important;
}
h1,h2,h4,h4,h6
{
  font-size: 4em !important;
  color: rgb(112,208,236);
}
p
{
  font-weight: 400 !important;
  color: #6C757D !important;
}
th,td
{
  border: none !important ;padding: 1em !important;
}
table
{
  text-align: center;
  letter-spacing: 2px;
  font-family: 'bubasaur', cursive;
}

.stat-table
{
  display: flex;
  padding: 2em !important;
  flex-direction: column;
  align-items: center !important;
  justify-content: center !important;
}
.btn 
{
  font-family: 'bubasaur', cursive !important;
  padding: 1em 2em !important;
  font-size: 1em !important;
  color: #ffffff !important;
  background-color: rgb(112,208,236) !important;
  border-radius: 1em;
  box-shadow: inset -6px -12px 16px 0 rgba(188, 246, 252, 0.4);
}
.team-pfp
{
  height: 256px !important;
  border-radius: 4em;
  padding: 2em;
}
a .ref-btn 
{
  
}
.heading
{
  margin: 1em auto;
  font-size: 5em !important;
  width: 100%;
  border-radius: 0.5em !important;
  padding: 0.25em 0em;
  background-color: rgb(220, 246, 255) !important;
  display: inline-block;
  filter: drop-shadow(32px 32px 24px rgba(39, 134, 145, 0.25));
  box-shadow: inset -6px -12px 16px 0 rgba(65, 178, 190, 0.25);
}
.rm
{
  margin: 2em auto;
}
.logo 
{
  margin-right: 2em !important;
}
.featurette
{
  margin: 4em 0em !important;
}
@media only screen and (max-width: 600px) {
  h1,h2,h3,h4,h4,h6
{
  font-size: 2em !important;
}
  .statistics-text
{
  display: flex !important;
  flex-wrap: wrap;
  align-items: center !important;
  text-align: center;
}
.stat-table
{
  display: grid !important;
  align-items: center !important;
  place-self: center !important;
  justify-content: center !important;
}
th,td
{
  border: none !important ;
  font-size: 0.5em;
  padding: 1em !important;
}
table
{
  text-align: center;
  letter-spacing: 2px;
  font-family: 'bubasaur', cursive;
}

}
.showcase
{
  height: 500px !important;
  overflow: hidden;
  position: absolute;
  width: 600px;
  background-image: url(./components/images/pygras/PygraShowcase.png);
  animation: showcase 10s ease-in-out infinite !important;
  background-size: 100% !important;
  background-repeat: repeat-y !important;
  right: 15%;
}

@keyframes
showcase
{
  0% {background-position: 0% 2%;}
  20% {background-position: 0% 25%;}
  40% {background-position: 0% 50%;}
  60% {background-position: 0% 75%;}
  80% {background-position: 0% 100%;}
    100% {background-position: 0% 126.28%;}
}


